import useValidator from "../../../hook/validate"
function LoginValidate(value, action = false) {
    return useValidator(value, [
        useValidator.isRequired("email", "Email khng c b trng"),
        useValidator.isEmail("email", "Khng phi nh dng Email"),
        useValidator.isRequired("password", "Mt Khu khng c b trng"),
    ]);
}

const field = {
    email: "kadita@gmail.com",
    password: ""
};

export { field };

export default LoginValidate;