import useValidator from "../../../hook/validate"
function AssignCustomerValidate(value, action = false) {
    return useValidator(value, [
        useValidator.isRequired("name", "Tn i tc khng c b trng"),
        useValidator.isRequired("qty", "S lng yu cu khng c b trng"),
        useValidator.minValue("qty", "S lng yu cu t nht l 1", 1),
    ])
}
export default AssignCustomerValidate;