import useValidator from "../../../hook/validate"
function SaveAdminValidate(value, action) {
    return useValidator(value, [
        useValidator.isRequired("email", "Email khng c b trng"),
        useValidator.isRequired("password", "Mt khu khng c b trng", action.current.isAdd),
        useValidator.isRequired("fullname", "H v Tn khng c b trng"),
        useValidator.isEmail("email", "Khng phi nh dng Email"),
        useValidator.min("password", 6, "Mt khu t nht 6 k t")
    ])
}

export default SaveAdminValidate;