import useValidator from "../../../hook/validate"
function AddFeedbackValidate(value, action = false) {
    return useValidator(value, [
        useValidator.isRequired("fullname", "Tn Nhn Vin khng c b trng"),
        useValidator.isRequired("staff_level", "Cp Bc khng c b trng"),
        useValidator.isRequired("phone", "S int Thoi khng c b trng"),
        useValidator.isPhone("phone", "Khng phi nh dng S in Thoi"),
        useValidator.isNumber("bank_account_number", "S Ti Khon phi nh dng s")
    ])
}
export default AddFeedbackValidate;