import React from "react";
import { Modal } from "../../../components";
import { WageContext } from "../../../context/WageContext";
import { TextField, Grid } from '@mui/material';

function SaveWage() {
    const themeWage = React.useContext(WageContext);
    const [isShow, setIshow] = React.useState(false);
    themeWage.handleAdd = function () {
        setIshow(true);
    }
    return (<Modal isShow={isShow} onClose={() => setIshow(false)} size="xl">
        <h2>Cp Nht Lng</h2>
        <Grid container spacing={2} sx={{ py: 2 }}>
            <Grid item xs={2}>
                <TextField
                    aria-readonly
                    label="MNS"
                    id="filled-size-small"
                    fullWidth
                    size="small" />
            </Grid>
            <Grid item xs={2}>
                <TextField
                    label="Cp bc"
                    id="filled-size-small"
                    fullWidth
                    size="small" />
            </Grid>
            <Grid item xs={4}>
                <TextField
                    label="Tn nhn vin"
                    id="filled-size-small"
                    fullWidth
                    size="small" />
            </Grid>
            <Grid item xs={4}>
                <TextField
                    label="ST"
                    id="filled-size-small"
                    fullWidth
                    size="small" />
            </Grid>
            <Grid item xs={4}>
                <TextField
                    label="Lng C Bn"
                    id="filled-size-small"
                    fullWidth
                    size="small" />
            </Grid>
            <Grid item xs={4}>
                <TextField
                    label="Lng doanh s"
                    id="filled-size-small"
                    fullWidth
                    size="small" />
            </Grid>
            <Grid item xs={4}>
                <TextField
                    label="Ph cp"
                    id="filled-size-small"
                    fullWidth
                    size="small" />
            </Grid>
            <Grid item xs={6}>
                <TextField
                    label="Tng"
                    id="filled-size-small"
                    fullWidth
                    size="small" />
            </Grid>
        </Grid>
    </Modal>)
}


export default SaveWage;
