import useValidator from "../../../../../../hook/validate"
function SaveStaffLevel(value, action = false) {
    return useValidator(value, [
        useValidator.isRequired("name", "Tn Cp Bc khng c b trng"),
        useValidator.isRequired("pay_level", "Lng Cp Bc khng c b trng"),
        useValidator.isRequired("allowance_pay", "Lng Ph Cp khng c b trng"),
    ])
}

export default SaveStaffLevel;