import useValidator from "../../../hook/validate"
function SaveCustomerValidate(value, action = false) {
    return useValidator(value, [
        useValidator.isRequired("surrogate", "Tn i tc khng c b trng"),
        useValidator.isPhone("phone", "Khng phi nh dng s in thoi"),
        useValidator.isRequired("name", "Tn Cng Ty khng c b trng"),
    ])
}
export default SaveCustomerValidate;