import useValidator from "../../../hook/validate"
function AddFeedbackValidate(value, action = false) {
    return useValidator(value, [
        useValidator.isRequired("customer_id", "Khch Hng khng c b trng"),
        useValidator.isRequired("report_title", "Tiu  Phn Hi khng c b trng"),
        useValidator.isRequired("report_content", "Ni Dung Phn Hi khng c b trng"),
    ])
}
export default AddFeedbackValidate;